<template>
  <form @submit.prevent="handleSubmit" class="upload-page-wrapper">
    <Navbar />
    <input
      ref="input"
      type="file"
      name="image"
      accept="image/png, image/jpeg"
      @change="setImage"
    />
    <div class="content d-flex justify-content-center align-items-center my-5">
      <section class="cropper-area w-50">
        <div v-show="initialImg" class="img-cropper text-center">
          <h4></h4>
          <Vue-cropper
            ref="cropper"
            class="my-3"
            :aspect-ratio="16 / 16"
            :src="initialImg"
          />
        </div>
      </section>
      <div class="actions mb-5">
        <button v-show="!isUploading" type="button" class="button btn btn-secondary text-light" @click.prevent="showFileChooser">
          
        </button>
      </div>
      <template v-if="!isUploading">
        <button v-if="initialImg" type="submit" class="btn btn-warning"></button>
      </template>
      <template v-else>
        <button class="btn btn-secondary" disabled>...</button>
      </template>
    </div>
  </form>
</template>

<script>
import VueCropper from 'vue-cropperjs'
import 'cropperjs/dist/cropper.css'

import { Toast } from './../utils/helpers'
import apis from './../apis'

import Navbar from './../components/Navbar.vue'

export default {
  components: {
    Navbar,
    VueCropper
  },
  data() {
    return {
      initialImg: '',
      initialImgFile: null,
      cropImg: '',
      cropImgFile: null,
      isUploading: false
    }
  },
  methods: {
    // 
    setImage(e) {
      const file = e.target.files[0]
      // 
      if (!this.checkFileSize(file)) {
        Toast.fire({
          icon: 'warning',
          title: '500kb'
        })
        return
      }
      // 
      this.$refs.cropper.replace(window.URL.createObjectURL(file))
      this.initialImg = window.URL.createObjectURL(file)
      this.initialImgFile = file
    },
    // 
    cropImage() {
      this.cropImg = this.$refs.cropper.getCroppedCanvas().toDataURL()
      const cropImgBlob = this.dataURLtoBlob(this.cropImg)
      this.cropImgFile = this.blobToFile(cropImgBlob, 'kitty.png')
    },
    // dataURLblob
    dataURLtoBlob(dataURL) {
      // byteString
      const byteString = window.atob(dataURL.split(',')[1])
      // mimeString
      const mimeString = dataURL.split(',')[0].split(':')[1].split(';')[0]
      const ab = new ArrayBuffer(byteString.length)
      const ia = new Uint8Array(ab)
      for (let i = 0; i < byteString.length; i++) {
          ia[i] = byteString.charCodeAt(i)
      }
      // ArrayBufferblob
      const blob = new Blob([ab], {type: mimeString})
      return blob
    },
    // blobfile
    blobToFile(blob, fileName) {
      const blobFile = new File([blob], fileName, {
        type: blob.type,
      })
      return blobFile
    },
    showFileChooser() {
      this.$refs.input.click()
    },
    checkFileSize(file) {
      return file.size <= 500 * 1024 // 500kb
    },
    // 
    handleSubmit() {
      if (this.isUploading) {
        Toast.fire({
          icon: 'warning',
          title: ''
        })
        return
      }
      this.cropImage()
      const formData = new FormData()
      formData.append('file', this.cropImgFile, this.initialImgFileName)
      this.uploadImage(formData)
    },
    // 
    clearData() {
      this.initialImg = ''
      this.initialImgFile = null
      this.cropImg = ''
      this.cropImgFile = null
    },
    async uploadImage(formData) {
      try {
        this.isUploading = true
        const response = await apis.uploadImage(formData)
        this.uploadedImg = response.data
        this.isUploading = false
        Toast.fire({
          icon: 'success',
          title: '!!'
        })
        this.clearData()
      } catch(error) {
        this.isUploading = false
        this.clearData()
        Toast.fire({
          icon: 'error',
          title: ''
        })
        console.log(error)
      }
    }
  }
}
</script>

<style scoped>
input {
  display: none;
}
.content {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}
</style>